import React, { useEffect, useState, useMemo } from 'react';
import './App.css';
import { TournamentProps, TournamentStatus, Player, TournamentSettings } from './interfaces';
import { GET } from './utils/requests';
import Pairings from './tournament/Pairings';
import CrossTable from './tournament/CrossTable';
import * as regexes from './utils/regexes';
import * as icons from './assets/icons';
import NotFound from './404';
import styles from './css/header.module.css';
import { FaLink } from 'react-icons/fa';

export default function Tournament(props: TournamentProps) {

    const id = props.match.params.id
	const [loaded, setLoaded] = useState(undefined as boolean | undefined);
    const [settings, setSettings] = useState({} as TournamentSettings);
    const [status, setStatus] = useState({} as TournamentStatus);
    const [players, setPlayers] = useState([] as Player[]);

    const playerDict: Map<string, Player> = useMemo(() => {
        if (!players.length) return new Map();
        let dict = new Map();
        for (let p of players) dict.set(p.id, p);
        return dict;
    }, [players]);

	const round = status.round || 0;
	
	useEffect(() => {
		GET({ url: '/tournament/' + id + '/getSettings'})
			.then(setSettings)
	}, [setSettings, id]);

    useEffect(() => {
		GET({ url: '/tournament/' + id + '/getStatus'})
			.then(setStatus)
			.then(() => setLoaded(true))
			.catch(() => setLoaded(false));
    }, [setStatus, id, setLoaded]);

	useEffect(() => {
		GET({ url: '/tournament/' + id + '/fetchPlayers'})
			.then(setPlayers)
	}, [setPlayers, id]);

    useEffect(() => {
        if (!round) return;
        let histories = new Array(round + 1);
        for (let i = 1; i < round + 1; i++) histories[i] = {
            match: 0,
            game: 0
        };
        playerDict.set('bye', {
            id: 'bye',
            firstName: 'BYE',
            histories
        } as any as Player);
	}, [playerDict, round]);
	
	const sections = ['pairings', 'standings'];
	const [section, setSection] = useState('pairings' as 'pairings' | 'standings');

	useEffect(() => {
		let hash = window.location.hash.slice(1);
		if (!sections.includes(hash)) return;
		setSection(hash as 'pairings' | 'standings');
	}, [window.location.hash])
	
	const streamBox = useMemo(() => {
		if (!settings.streamURL) return false;
		let matches = settings.streamURL.match(regexes.streamLink);
		if (!matches) return null;
		let [domain, path] = matches.slice(1);
		let site: 'twitch' | 'youtube' = domain.split('.').shift() as 'twitch' | 'youtube';
		const streamDict = {
			twitch: 'https://twitch.tv/',
			youtube: 'https://youtube.com/'
		};
		return (
			<a 
				href={streamDict[site] + path}
				className={['header-button', 'stream-link', site + '-link'].join(' ')}
				target='_blank'
				rel='noopener noreferrer'
			>
				<div><img src={icons[site]} alt={site}/></div>
				<div><h2>Commentary</h2></div>
			</a>
		);
	}, [settings.streamURL]);

	if (typeof loaded === 'undefined') return null;
	if (!loaded) return <NotFound />;

    return (

        <div id='main'>
            <div className='container'>
				<div className={styles.container}>
					<div className={styles.profilePicture}><img src={settings.logoURL} /></div>
					<div><h1>{status.name}</h1></div>
					<a className={['header-button', styles.website].join(' ')} 
						href={settings.profileURL}
						target='_blank'
						rel='noopener noreferrer'
					>
						<FaLink /> Website
					</a>
				</div>

				<section>
					<div className='button-list'>
						<a href='#pairings' className={['header-button', section === 'pairings' ? 'selected' : ''].join(' ')}>
							<div className='full-text'><h2>Pairings</h2></div>
						</a>
						<a href='#standings' className={['header-button', section === 'standings' ? 'selected' : ''].join(' ')}>
						<div className='full-text'><h2>Standings</h2></div>
						</a>
						{streamBox}
					</div>
				</section>
				<section>
					{section === 'pairings' ? <Pairings
						id={props.match.params.id}
						playerDict={playerDict}
						status={status}
						settings={settings}
					/> : null}
					{section === 'standings' ? <CrossTable						
						setDisplayPlayer={() => {}}
						show={() => {}}
						status={status}
						players={Array.from(playerDict.values())}
						settings={settings}
					/> : null}
				</section>
            </div>
        </div>
    );
}
